import React, { useState, useEffect } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import styled from 'styled-components';
import { navDelay, loaderDelay } from '@utils';
import { usePrefersReducedMotion } from '@hooks';
import { email } from '@config';
import { Icon } from '@components/icons'
import Emoji from '@components/emoji';


const StyledHeroSection = styled.section`
  ${({ theme }) => theme.mixins.flexCenter};
  flex-direction: column;
  align-items: flex-start;
  min-height: 100vh;
  padding: 0;

  @media (max-width: 480px) and (min-height: 700px) {
    padding-bottom: 10vh;
  }

  h1 {
    margin: 0 0 30px 4px;
    color: var(--green);
    font-family: var(--font-mono);
    font-size: clamp(var(--fz-sm), 5vw, var(--fz-md));
    font-weight: 400;

    @media (max-width: 480px) {
      margin: 0 0 20px 2px;
    }
  }

  h3 {
    margin-top: 10px;
    color: var(--slate);
    line-height: 0.9;
  }

  p {
    margin: 0 0 0;
    max-width: 640px;
  }

  a {
    padding-top: 40px;
    padding-right: 40px;

    &:hover,
    &:focus {
      transform: translateY(-3px);
    }

    svg {
      width: 30px;
      height: 30px;
    }
  }

  
`;

const Hero = () => {
  const [isMounted, setIsMounted] = useState(false);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    const timeout = setTimeout(() => setIsMounted(true), navDelay);
    return () => clearTimeout(timeout);
  }, []);

  const one = <h1>Hi there<Emoji symbol =""></Emoji>, my name is</h1>;
  const two = <h2 className="big-heading">Efe Evci.</h2>;
  const three = <h3 className="big-heading">I am a software engineer.</h3>;
  const four = (
    <>
      <p>
        Currently, I am finishing up my bachelor's degree of computer engineering.
      </p>
    </>
  );
  
  const five = (
      <div>
          <a href= "https://github.com/efeberkeevci">
            <Icon name="GitHub" />
          </a>

          <a href= "https://www.linkedin.com/in/efe-evci">
            <Icon name="Linkedin" />
          </a>

          <a href= "mailto:efeberkeevci@gmail.com">
            <Icon name="Email" />
          </a>
      </div>
        
  );
  
  const items = [one, two, three, four, five];
  //const items = [one, two, three, four];

  return (
    <StyledHeroSection>
      {prefersReducedMotion ? (
        <>
          {items.map((item, i) => (
            <div key={i}>{item}</div>
          ))}
        </>
      ) : (
        <TransitionGroup component={null}>
          {isMounted &&
            items.map((item, i) => (
              <CSSTransition key={i} classNames="fadeup" timeout={loaderDelay}>
                <div style={{ transitionDelay: `${i + 1}00ms` }}>{item}</div>
              </CSSTransition>
            ))}
        </TransitionGroup>
      )}
    </StyledHeroSection>
  );
};

export default Hero;
