import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { srConfig, email } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';
import Emoji from '@components/emoji';


const StyledContactSection = styled.section`
  max-width: 600px;
  margin: 0 auto 100px;
  text-align: center;

  @media (max-width: 768px) {
    margin: 0 auto 50px;
  }

  .overline {
    display: block;
    margin-bottom: 20px;
    color: var(--green);
    font-family: var(--font-mono);
    font-size: var(--fz-md);
    font-weight: 400;

    &:before {
      bottom: 0;
      font-size: var(--fz-sm);
    }

    &:after {
      display: none;
    }
  }

  .title {
    font-size: clamp(40px, 5vw, 60px);
  }

  .email-link {
    ${({ theme }) => theme.mixins.bigButton};
    margin-top: 50px;
    margin-left: 10px;
    margin-right: 10px;
  }
`;

const Contact = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  return (
    <StyledContactSection id="contact" ref={revealContainer}>
      <h2 className="numbered-heading overline">Whats Next?</h2>

      <h2 className="title">Get In Touch</h2>

      <p>
        I value information exchange highly. Whether you want to talk about computer science, chat about your favorite soccer team, 
        brainstorm on your "next big thing", or just to say hi <Emoji symbol =""></Emoji>
      </p>

      <div> 
        <a className="email-link" href={`mailto:${email}`}>
          Email me
        </a>

        <a className="email-link" href="https://calendly.com/efeevci/30-min-chat">
        Book a 30-min with me
        </a>
      </div>
    </StyledContactSection>
  );
};

export default Contact;
